'use client';
import { useState, useEffect } from 'react';
import { Loader2 } from 'lucide-react';
import { RiskAssessmentCard } from '@/components/dashboard/RiskAssessmentCard';
import { ImpactAnalysisCard } from '@/components/dashboard/ImpactAnalysisCard';
import { RecommendationsCard } from '@/components/dashboard/RecommendationsCard';
import { OverviewMetrics } from '@/components/dashboard/OverviewMetrics';

export default function DashboardPage() {
    const [data, setData] = useState(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/dashboard`)
            .then(res => res.json())
            .then(data => {
                setData(data);
                setIsLoading(false);
            });
    }, []);

    if (isLoading) return <div className="flex justify-center items-center h-full"><Loader2 className="w-12 h-12 animate-spin text-corporate-red" /></div>;
    if (!data) return <div>Failed to load dashboard data.</div>;

    return (
        <div className="space-y-8">
            <header>
                <h1 className="text-3xl font-bold text-gray-900">Regulatory Dashboard</h1>
                <p className="text-lg text-gray-600 mt-1">A consolidated overview of your regulatory landscape.</p>
            </header>

            <OverviewMetrics data={data} />
            <RiskAssessmentCard data={data.riskAssessment} />
            <ImpactAnalysisCard data={data.impactAnalysis} />
            <RecommendationsCard data={data.recommendations} />
        </div>
    );
}